.obra {
    padding-bottom: 10rem;
}
.datos {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 0rem;
    h3 {
        margin-top: 0;
        margin-bottom: 0;
    }
}
.galery {
    width: 100%;
    max-width: 120rem;
    margin-left: auto;
    margin-right: auto;
    display: grid;
    grid-template-columns: repeat(auto-fit,133px);
    grid-auto-rows: 200px;
    justify-content: center;
    gap: 1.2rem;
    .galery-i {
        picture {
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
        clip-path: polygon(50% 0%,100% 25%,100% 75%,50% 100%,0% 75%,0% 25% );
        grid-column: span 2;
        height: 280px;
        
        &:hover{
            filter: none;
            transition: all 250ms ease-in-out;
        }
        &:first-of-type {
            grid-column: 2 / span 2;
        }
    }
}
.overlay {
    position: fixed;
    left: 0;
    top: 0;
    width: 100vw;
    height: 100vh;
    background: $blackT;
    z-index: 10000;
    display: flex;
    justify-content: center;
    align-items: center;
    transition: all .7s ease ;
    visibility: hidden;
    opacity: 0;
    .slideshow{
        width: 90%;
        height: 90%;
        background-color: $whiteT;
        color: $black;
        position: relative;
        display: flex;
        justify-content: center;
        align-items: center;
        .btn_cerrar {
            position: absolute;
            top: -15px;
            right: -15px;
            font-size: 2rem;
            background-color: $grisOscuro;
            color: $white;
            width: 30px;
            height: 30px;
            border-radius: 50%;
            border: 1px solid $grisOscuro;
            display: flex;
            justify-content: center;
            align-items: center;
            padding: .5rem;
            transition: all .2s ease;
            &:hover{
                transform: scale(1.2);
                cursor: pointer;
                background-color: $red;
            }
        }
        .botones {
            position: absolute;
            font-size: 2rem;
            transform: translateY(-50%);
            display: flex;
            justify-content: center;
            align-items: center;
            top: 50%;
            color: $white;
            background-color: $grisClaro;
            width: 5rem;
            height: 5rem;
            border-radius: 50%;
            border: 1px solid $grisClaro;
            padding: .5rem;
            transition: all .2s ease;
            &:hover{
                transform: scale(1.05);
                cursor: pointer;
                background-color: $grisOscuro;
            }
        }
        .back{
            left: 2rem;
        }
        .next{
            right: 2rem;
        }
        img {
            height: 98%;
            width: 100%;
            object-fit: contain;
        }
    }
}
/* media querrys*/
@media screen and (min-width:270px) and (max-width:504px){
    .overlay {
        .slideshow {
            width: 92%;
            height: 92%;
            .botones {
                width: 3rem;
                height: 3rem;
            }
            .back{
                left: .5rem;
            }
            .next{
                right: .5rem;
            }
            img {
                height: 100%;
                width: 85%;
            }
        }
    }
}
@media screen and (min-width:505px) and (max-width:685px){
    .galery {
        .galery-i {
            &:nth-of-type(odd) {
                grid-column: 2 / span 2;
            } 
        }
    }
}
@media screen and (min-width:686px) and (max-width:862px){
    .galery {
        .galery-i {
            &:nth-of-type( 3n + 1) {
                grid-column: 2 / span 2;
            } 
        }
    }

}
@media screen and (min-width:863px) and (max-width:1038px){
    .galery {
        .galery-i {
            &:nth-of-type( 4n + 1) {
                grid-column: 2 / span 2;
            } 
        }
    }
}
@media screen and (min-width:1039px) and (max-width:1215px){
    .galery {
        .galery-i {
            &:nth-of-type( 5n + 1) {
                grid-column: 2 / span 2;
            } 
        }
    }
}
@media screen and (min-width:1216px) and (max-width:1391px){
    .galery {
        .galery-i {
            &:nth-of-type( 6n + 1) {
                grid-column: 2 / span 2;
            } 
        }
    }
}
@media screen and (min-width:1392px){
    .galery {
        .galery-i {
            &:nth-of-type( 7n + 1) {
                grid-column: 2 / span 2;
            } 
        }
    }
}

@include desktop{
    .obra {
        transform: scale(.92);
    }
}
@include tablet{
    .obra {
        transform: scale(.92);
        .galery {
            grid-template-columns: repeat(auto-fit,153px);
        }
    }
}