.contenedor-s {
    margin: 0px auto;
    .somos {
        width: 100%;
        height: 100vh;
        background-image: url(/build/img/somos.jpg);
        background-attachment: fixed;
        background-position: center;
        background-size: cover;
        position: relative;
        overflow: hidden;
        text-align: center;
        .color {
            top: 0;
            width: 100%;
            height: 100%;
            background-color: $grisOscuroT;
        }
        .textos {
            padding: 20rem;
            p {
                text-align: justify;
            }
        }
        .skew {
            position: absolute;
            bottom: 0;
            left: 0;
            border-width:0 0 50vh 100vw;
            border-style: solid;
            border-color: transparent transparent $grisOscuro;
        }
    }
}
.contenedor {
    .mis-vis {
        width:100%;
        .rows {
            width:100%;
            display: flex;
            justify-content: center;
            .row {
                width: 100%;
                padding: 0 3rem;
            }
        }
    }
.cont-valores {
        max-width: 100%;
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
        .valor {
            width: 40rem;
            height: 33rem;
            margin: 1rem;
            overflow: hidden;

            .titulo {
                padding: .5rem;
            }
            p {
                margin: 0;
                padding: 1rem;
                font-size: 1.4rem;
            }
            .contenido{
                display: flex;
                flex-direction: column;
                align-items: center;
                .qr{
                    picture{
                        width: 14rem;
                        height: 14rem;
                    }
                }
                .btn{
                    letter-spacing: .3rem;
                    position: absolute; /* Imprescindible */
                    width: 100vw;
                    left: 50%; /* X */
                    top: 100%; /* Y */
                    transform: translate(-50%, -100%); /* Reposicionar X,Y */
                    .boton{
                        margin: 0;
                    }
                    }

            }
        }
    }
}
/* medias */

@include tablet {
    .contenedor-s {
        .somos {
            .textos{
                padding: 15rem;
            }
        }
    }
    .contenedor {
        .cont-valores {
            .valor {
                width: 30rem;
                height: 36rem;
            }
        }
    }
}
@include telefono {
    .contenedor-s {
        .somos {
            .textos {
                padding: 10rem 5rem;
            }
            .skew{
                border-width:0 0 30vh 100vw;
            }
        }
    }
    .contenedor {
        
        .mis-vis {
            margin-bottom: 1.5rem;
            .rows {
                flex-wrap: wrap;
            }
        }
    }
}    
@include telefono-S {
    .contenedor-s {
        .somos {
            .textos {
                padding: 7rem 5rem;

            }
            .skew{
                border-width:0 0 25vh 100vw;
            }
        }
    }
}
@include telefono-SS {
    .contenedor-s {
        .somos {
            .textos {
                padding: 7rem 3rem;

            }
            .skew{
                border-width:0 0 25vh 100vw;
            }
        }
    }
}

