.servicios {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    .servicio {
        width: 30rem;
        height: 39rem;
        margin: .3rem;
        overflow: hidden;
        .contenido {
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            text-align: center;
            .img_service {
                display: flex;
                justify-content: center;
                align-items: center;
                width: 15rem;
                height: 15rem;
                margin-top: .5rem;
                box-shadow: 0 0 2rem $grisOscuroT;
                border: 1px solid $grisClaro;
                border-radius: 50%;
                overflow: hidden;
                img {
                    object-fit: cover;
                    width: 100%;
                    height: 100%;
                    }
                }
            }
        }
        h3{
            font-size: 2rem;
        }
        p {
            padding: 1rem;
            letter-spacing: .1rem;
            margin-bottom: 0;
        }
        .btn{
        letter-spacing: .3rem;
        position: absolute; /* Imprescindible */
        width: 100vw;
        left: 50%; /* X */
        top: 100%; /* Y */
        transform: translate(-50%, -100%); /* Reposicionar X,Y */
        .boton{
            margin: 0;
        }
        }
    }

